.popup–°ommon {
    display: flex;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s linear 0.2s, opacity 0.2s linear;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgb(0, 0, 0, 0.5);
    align-items: center;
}

.popup_is-opened {
    visibility: visible;
    opacity: 1;
    transition-delay: 0s;
}

.popup {
    top: calc(50vh - 30px);
    margin: 0 auto;
    width: auto;
    max-width: 280px;
    background-color: #fff;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    position: relative;

    padding: 20px 30px;
}

button {
    padding: 0;
    border: 0;
    width: 20px;
    height: 20px;
    &:hover {
        cursor: pointer;
        box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.15);
    }
}
.link {
    margin: 0 20px 0 0;
}

.copy {
    margin-top: auto;
    margin-bottom: auto;
    background-color: white;
    width: 20px;
    height: 20px;
}